<template>
  <div class="tab">
    <cube-tab-bar
    :useTransition="false"
    :show-slider="true"
    v-model="selectedLabel"
    :data="tabs"
    ref="tabBar"
    class="border-bottom-1px"
    >
    </cube-tab-bar>
    <div class="slide-wrapper">
      <cube-slide
      :initial-index="index"
      :loop="false"
      :auto-play="false"
      :show-dots="false"
      ref="slide"
      @change="onChange"
      @scroll="onScroll"
      :options="slideOptions"
      >
      <cube-slide-item
      v-for="(tab, index) in tabs"
      :key="index"
      >
        <component :is="tab.component" :data="tab.data" ref="component"></component>
      </cube-slide-item>
      </cube-slide>
    </div>
  </div>
</template>
<script>
import Goods from 'components/goods/goods'
import Ratings from 'components/ratings/ratings'
import Seller from 'components/seller/seller'

export default {
  name: 'tab',
  components: {
    Goods,
    Ratings,
    Seller
  },
  props: {
    tabs: {
      type: Array,
      default() {
        return []
      }
      // default: () => [] // 
    },
    // APPinitialIndexTab
    initialIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      index: this.initialIndex,
      slideOptions: {
        listenScroll: true,
        probeType: 3,
        directionLockThreshold: 0 // 
      }
    }
  },
  computed: {
    selectedLabel: {
      get() {
        return this.tabs[this.index].label
      },
      set(newVal) {
        // console.log(newVal) // newValtabslabel
        this.index = this.tabs.findIndex(value => {
          // console.log(value) // valuelabel {label:''}
          return value.label === newVal
        })
      }
    }
  },
  mounted() {
    this.onChange(this.index)
  },
  methods: {
    onChange(currentSildeItem) {
      // cube-sildechange(value)valuesildeItem
      this.index = currentSildeItem
      const component = this.$refs.component[currentSildeItem]
      component.fetch && component.fetch()
      // if (component.fetch) {
      //   component.fetch()
      // }
    },
    onScroll(pos) {
      // console.log(pos) // x
      // cube-sildescroll(value)optionslinstenScroll:trueprobeType:3scrollvalueitem
      const tabBarWidth = this.$refs.tabBar.$el.clientWidth // this.$refs.tabBar.$elDOM
      const slideWidth = this.$refs.slide.slide.scrollerWidth // this.$refs.slideslidethis.$refs.slide.slideBscroll
      const transform = (-pos.x / slideWidth) * tabBarWidth
      this.$refs.tabBar.setSliderTransform(transform)
      // console.log(tabBarWidth) // 375
      // console.log(slideWidth) // 1125
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~common/stylus/variable'
.tab
  display: flex
  flex-direction: column
  height: 100%
  >>> .cube-tab
    padding: 10px 0
  .slide-wrapper
    flex: 1
    overflow: hidden
</style>
