<template>
  <div>
    <div class="shopcart">
      <div class="content" @click="toggleList">
        <div class="content-left">
          <div class="logo-wrapper">
            <div class="logo" :class="{ 'highlight': totalCount > 0 }"> <!-- totalCount0highlighticon -->
              <i class="icon-shopping_cart" :class="{ 'highlight': totalCount>0 }"></i>
            </div>
            <div class="num" v-show="totalCount > 0">
              <bubble :num="totalCount"></bubble>
            </div>
          </div>
          <div class="price" :class="{ 'highlight': totalPrice > 0 }">{{totalPrice}}</div>
          <div class="desc">{{deliveryPrice}}</div>
        </div>
        <div class="content-right">
          <div class="pay" :class="payClass" @click="pay">{{payDesc}}</div>
        </div>
      </div>
      <div class="ball-container">
        <div v-for="(ball, index) in balls" :key="index">
          <transition
          @before-enter="beforeDrop"
          @enter="dropping"
          @after-enter="afterDrop">
            <div class="ball" v-show="ball.show">
              <div class="inner inner-hook"></div> <!--  -->
            </div>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Bubble from '../bubble/bubble.vue'

const BALL_LEN = 10
const innerClsHook = 'inner-hook'

function createBalls() {
  const ret = []
  for (let i = 0; i < BALL_LEN; i++) {
    ret.push({
      show: false
    })
  }
  return ret
}

export default {
  name: 'ShopCart',
  components: {
    Bubble
  },
  props: {
    selectFoods: {
      type: Array,
      default: () => []
    },
    deliveryPrice: {
      type: Number,
      default: 0
    },
    minPrice: {
      type: Number,
      default: 0
    },
    fold: {
      type: Boolean,
      default: true
    },
    sticky: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      balls: createBalls(), // balls[{show:false}*10]
      listFold: this.fold // propsfold,datalistFold
    }
  },
  computed: {
    totalPrice() {
      let total = 0
      this.selectFoods.forEach(food => {
        total += food.price * food.count
      })
      return total
    },
    totalCount() {
      let count = 0
      this.selectFoods.forEach(food => {
        count += food.count
      })
      return count
    },
    payDesc() {
      if (this.totalPrice === 0) {
        return `${this.minPrice}`
      } else if (this.totalPrice < this.minPrice) {
        const diff = this.minPrice - this.totalPrice
        return `${diff}`
      } else {
        return ''
      }
    },
    payClass() {
      if (!this.totalPrice || this.totalPrice < this.minPrice) {
        return 'not-enough'
      } else {
        return 'enough'
      }
    }
  },
  watch: {
    fold(newVal) {
      this.listFold = newVal // watchfoldshop-cart-stickyfold: 'listFold'
    },
    totalCount(newVal) {
      // totalCount0
      if (!this.listFold && !newVal) {
        // console.log(Boolean(!0)) // Boolean(0)false,Boolean(!0)true
        this._hideShopCartList()
      }
    }
  },
  created() {
    this.dropBalls = [] // data
    // this.listFold = true // 
  },
  methods: {
    drop(el) {
      for (let i = 0; i < this.balls.length; i++) {
        const ball = this.balls[i]
        if (!ball.show) {
          ball.show = true
          ball.el = el // DOMball
          // console.log(ball.el) //ball.elcart-add icon-add_circle
          this.dropBalls.push(ball) // [{show:true, el: div.cart-add.icon-add_circle} * 10]
          return
        }
      }
    },
    beforeDrop(el) {
      // console.log(el) // elball
      const ball = this.dropBalls[this.dropBalls.length - 1]
      const rect = ball.el.getBoundingClientRect()
      // getBoundingClientRect()DOM
      const x = rect.left - 32
      const y = -(window.innerHeight - rect.top - 22)
      el.style.display = '' // 
      el.style.transform = el.style.webkitTransform = `translate3d(0, ${y}px, 0)` // ,Y
      // console.log(el.style.transform)
      // console.log(el.style.webkitTransform)
      // let a = 1
      // console.log(a) // 1
      // let b = 2
      // console.log(b) // 2
      // a = b = 3
      // console.log(a, b) // 3 3
      const inner = el.getElementsByClassName(innerClsHook)[0]
      inner.style.transform = inner.style.webkitTransform = `translate3d(${x}px, 0, 0)`
    },
    dropping(el, done) {
      // before-enterbeforeDrop(el)
      // 
      this._reflow = document.body.offsetHeight
      el.style.transform = el.style.webkitTransform = 'translate3d(0, 0, 0)'
      const inner = el.getElementsByClassName(innerClsHook)[0]
      inner.style.transform = inner.style.webkitTransform = 'translate3d(0, 0, 0)'
      el.addEventListener('transitionend', done) // transitionend
    },
    afterDrop(el) {
      const ball = this.dropBalls.shift()
      if (ball) {
        ball.show = false
        el.style.display = 'none'
      }
    },
    toggleList() {
      if (this.listFold) {
        // return
        if (!this.totalCount) {
          return
        }
        this.listFold = false
        this._showShopCartList()
        this._showShopCartSticky() // creteAPIshop-cart-listshopcart
      } else {
        this.listFold = true
        this._hideShopCartList()
      }
    },
    pay(e) {
      if (this.totalPrice < this.minPrice) {
        // console.log('') // totalPrice00
        return
      }
      this.$createDialog({
        title: '',
        content: `${this.totalPrice}`
      }).show()
      // console.log(this.yanshi) // Vue
      // console.log(this.yanshi.show())
      // setTimeout(yanshi(),1000)
      e.stopPropagation() // 
      // this.dialogComp = this.$createDialog({
      //   title: '',
      //   content: `${this.totalPrice}`
      // })
      // this.dialogComp.show()
    },
    _showShopCartList() {
      // shopCartListCompthis.$createShopCartList
      this.shopCartListComp =
        this.shopCartListComp ||
        this.$createShopCartList({
          $props: {
            selectFoods: 'selectFoods' // 
          },
          $events: {
            hide: () => {
              // console.log('hide')
              this.listFold = true
            }, // APIhide,hidethis.listFoldtrue
            leave: () => {
              this._hideShopCartSticky()
            },
            add: el => {
              this.shopCartStickyComp.drop(el)
            }
          }
        })
      this.shopCartListComp.show()
    },
    _showShopCartSticky() {
      this.shopCartStickyComp =
        this.shopCartStickyComp ||
        this.$createShopCartSticky({
          $props: {
            selectFoods: 'selectFoods',
            deliveryPrice: 'deliveryPrice',
            minPrice: 'minPrice',
            fold: 'listFold',
            list: this.shopCartListComp
          }
        })
      this.shopCartStickyComp.show()
    },
    _hideShopCartList() {
      // this.shopCartListComp.hide()
      const comp = this.sticky ? this.$parent.list : this.shopCartListComp // shop-cart-listshop-cart-sticky
      // comp.hide && comp.hide()
      // foodshopCartListtotalCount_hideShopCartListthis.$parent.listcomp.hidecomp
      comp && comp.hide && comp.hide()
    },
    _hideShopCartSticky() {
      this.shopCartStickyComp.hide()
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~common/stylus/mixin'
@import '~common/stylus/variable'
.shopcart
  height 100%
  .content
    display flex
    background $color-background
    // font-size 0
    color $color-light-grey
    .content-left
      flex 1
      .logo-wrapper
        display inline-block
        vertical-align top
        position relative
        top -10px
        margin 0 12px
        padding 6px
        width 56px
        height 56px
        box-sizing border-box
        border-radius 50%
        background $color-background
        .logo
          width 100%
          height 100%
          border-radius 50%
          text-align center
          background $color-dark-grey
          &.highlight
            background $color-blue
          .icon-shopping_cart
            line-height 44px
            font-size $fontsize-large-xxx
            color $color-light-grey
            &.highlight
              color $color-white
        .num
          position absolute
          top 0
          right 0
      .price
        display inline-block
        vertical-align top
        margin-top 12px
        line-height 24px
        padding-right 12px
        box-sizing border-box
        border-right 1px solid rgba(255, 255, 255, 0.1)
        font-weight 700
        font-size $fontsize-large
        &.highlight
          color $color-white
      .desc
        display inline-block
        vertical-align top
        margin 12px 0 0 12px
        line-height 24px
        font-size $fontsize-small-s
    .content-right
      flex 0 0 105px
      width 105px
      .pay
        height 48px
        line-height 48px
        text-align center
        font-weight 700
        font-size $fontsize-small
        &.not-enough
          background $color-dark-grey
        &.enough
          background $color-green
          color $color-white
  .ball-container
    .ball
      position fixed
      left 32px
      bottom 22px
      z-index 200
      transition all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41) // 
      .inner
        width 16px
        height 16px
        border-radius 50%
        background $color-blue
        transition all 0.4s linear
</style>
